import axios from 'axios'
import React, { useState } from 'react'
import { useEffect } from 'react'
import Card from 'react-bootstrap/Card';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Link } from 'react-router-dom';



function Home() {
    const [contacts,setcontacts]=useState([])
    //function api call
  const getAllContacts=async()=>{
    const result=await axios.get('/db.json')
    setcontacts(result.data.contacts);
  
  }
  useEffect(()=>{
   getAllContacts()
  },[])
  console.log(contacts);
    return (
    <div>
  
      <Container >
      <Row >
        {
          contacts.length>0?contacts.map(i=>
            
              <Col  xs={12} sm={6} lg={4} md={6}>
                <Link to={`/view/${i.name}`} style={{textDecoration:'none'}}>
              <Card  style={{ width: '18rem',height:'550px' }} className='mt-3 bg-dark'>
        <Card.Img
        style={{height:'445px',marginLeft:'0px'}}
        variant="top" src={i.profile}/>
        <Card.Body>
          <Card.Title className='text-center text-danger'>{i.name}</Card.Title>
          
        </Card.Body>
      </Card>
      </Link>
      </Col>
            
            ):<h1>No  data present</h1>
        }
      </Row>
      </Container>
      </div>
    )
}

export default Home