import axios from "axios";
import React, { useEffect, useState } from "react";
import { Row, Col } from "react-bootstrap";
import { useParams } from "react-router-dom";
import ListGroup from "react-bootstrap/ListGroup";
// import './Singleperson.css'

function Singleperson() {
 
    const[allcontacts,setcontacts] = useState([]);

  const param = useParams();

  const name=param.name

  const getAllContacts=async()=>{
    const result=await axios.get('/db.json')
    setcontacts(result.data.contacts);
  
  }

  useEffect(()=>{
    getAllContacts();
  },[]);
 
  const singlecontact=allcontacts.find(i=>i.name==name);
  
  return (
    <div className="bg-dark">
      
      {singlecontact ? (
        <Row >
          <Col 
            lg={6}
            sm={12}
            xs={12}
            md={6}
            className="py-5 ps-5 mt-5 mb-5 ms-5"
          >
            <img
              style={{ height: "400px"}}
             
              className="w-100 img-fluid"
              src={singlecontact.profile}
              alt=""
            />
          </Col>
          <Col className="text-start w-100">
            <h1 className="text-center mt-3 text-success">{singlecontact.name}</h1>
            <ListGroup className="me-lg-5 pe-lg-5 w-100 fs-4">
              <ListGroup.Item className="mt-4">
                <span className="text-light">
                  <b>Email : </b>
                </span>
                {singlecontact.email}
              </ListGroup.Item>
              <ListGroup.Item className="mt-4">
                <span className="text-light">
                  <b>Mobile : </b>
                </span>
                {singlecontact.mobile}
              </ListGroup.Item>
              <ListGroup.Item className="mt-4">
                <span>
                  <b className="text-light">Location : </b>
                </span>
                {singlecontact.location}
              </ListGroup.Item>
            </ListGroup>
          </Col>
        </Row>
      ) : (
        <Row>
          <h1>No Data</h1>
        </Row>
      )}
    </div>
  );
}

export default Singleperson